const initialState = {
  profile: {},
  auth: false,
  error: null
}

export default function userReducer(state = initialState, action) {
  switch (action.type) {
    //  
    case "USER_REGISTER_SUCCESS":
      return {
        profile: action.payload,
        auth: true,
        error: null
      }

    //   
    case "USER_REGISTER_ERROR":
      return {
        profile: {},
        auth: false,
        error: action.payload
      }

    //   
    case "USER_LOGOUT":
      return {
        profile: {},
        auth: false,
        error: null
      };

    //   
    case "USER_LOGIN":
      return {
        profile: action.payload,
        auth: true,
        error: null
      };

    //   
    case "USER_LOGIN_ERROR":
      return {
        profile: {},
        auth: false,
        error: action.payload
      };

    default:
      //  
      return state;
  }
}