const initialState = {
  data: {},
  error: null,
  waiting: false
}

export default function electronicReducer(state = initialState, action) {
  switch (action.type) {
    //    
    case "LOAD_ELECTRONIC":
      return {
        data: {},
        error: null,
        waiting: true
      }
    //      
    case "LOAD_SUCCESS_ELECTRONIC":
      return {
        data: action.payload,
        error: null,
        waiting: false
      }

    //  
    case "LOAD_ERROR_ELECTRONIC":
      return {
        data: {},
        error: action.payload,
        waiting: false
      }

    default:
      //  
      return state;
  }
}